import "./navbar.css"
import {Link} from "react-router-dom"
function navBar() {
    return(
        <span class="nav">

        <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/contact">Contact Us</Link></div>
         <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/item">Items</Link></div>
         <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/login">Login</Link></div>
         <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/signup">Sign Up</Link></div>
         <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/product">Products</Link></div>
         <div><Link style={{textDecoration:"none" ,color:"white" ,fontSize:"40px"}} to="/user">User</Link></div>
        </span>
    )
}
export default navBar;